import { Component, EventEmitter, Inject, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { IManagedObject } from '@c8y/client';
import { ContextRouteService, DisplayOptions, Permissions } from '@c8y/ngx-components';
import { Subscription } from 'rxjs';
import { SUB_ASSETS_CONFIG, SubAssetsConfig } from './sub-assets.model';
import { SubAssetsService } from './sub-assets.service';

@Component({
  selector: 'c8y-sub-assets',
  templateUrl: './sub-assets.component.html'
})
export class SubAssetsComponent implements OnInit, OnDestroy {
  group: IManagedObject;
  currentGroupId: string;
  showChildrenForDevice: IManagedObject;
  title: string;
  toggle = false;
  showAddGroup = false;
  showAssignDevices = false;
  requestInProgress: boolean;
  showAssignChildDevices = false;
  refresh: EventEmitter<any> = new EventEmitter();
  filterable = true;
  shouldDisableAddGroup = false;
  shouldDisableAssignDevices = false;
  shouldShowAssetsProperties = false;
  displayOptions: DisplayOptions;
  customProperties: IManagedObject[] = [];

  private dataSub: Subscription;

  constructor(
    private route: ActivatedRoute,
    private subAssetsService: SubAssetsService,
    private contextRouteService: ContextRouteService,
    private permissionsService: Permissions,
    @Inject(SUB_ASSETS_CONFIG) public moduleConfig: SubAssetsConfig
  ) {
    this.dataSub = this.route.parent.data.subscribe(({ contextData }) => {
      this.init(contextData);
    });
  }

  async ngOnInit() {
    this.displayOptions = {
      striped: true,
      bordered: false,
      gridHeader: true,
      filter: true
    };
  }

  async init(contextData) {
    this.group = { ...contextData };
    this.title = this.group.name;
    this.currentGroupId = this.group.id;
    this.shouldDisableAddGroup = !(await this.permissionsService.canEdit(
      ['ROLE_INVENTORY_ADMIN', 'ROLE_INVENTORY_CREATE'],
      this.group
    ));
    this.shouldDisableAssignDevices = !(await this.subAssetsService.canAssignDevice(this.group));
    this.customProperties = await this.subAssetsService.getCustomProperties(this.group);
    this.shouldShowAssetsProperties =
      this.moduleConfig.showProperties && this.customProperties.length > 0;
  }

  groupChange(group: IManagedObject): void {
    this.group = group;
    this.title = group.name;
    this.contextRouteService.setContext(this.route, group);
    this.contextRouteService.refreshContext();
    this.refresh.emit();
  }

  isSmartGroup() {
    return this.subAssetsService.isSmartGroup(this.group);
  }

  ngOnDestroy(): void {
    if (this.dataSub) {
      this.dataSub.unsubscribe();
    }
  }
}
