import { Component, Input, ViewChild } from '@angular/core';
import { IManagedObject } from '@c8y/client';
import {
  ConfirmModalComponent,
  GainsightService,
  gettext,
  ModalLabels,
  Status,
  StatusType
} from '@c8y/ngx-components';
import { TranslateService } from '@ngx-translate/core';
import { Subject } from 'rxjs';
import { PRODUCT_EXPERIENCE } from '../shared/sub-assets.model';

@Component({
  selector: 'c8y-unassign-modal',
  templateUrl: './unassign-modal.component.html'
})
export class UnassignModalComponent {
  CURRENT_LOCATION = location.href;
  @Input() asset: IManagedObject;
  @ViewChild('modalRef', { static: false }) modalRef: ConfirmModalComponent;
  message: string;
  closeSubject: Subject<boolean> = new Subject();
  labels: ModalLabels = { ok: gettext('Unassign'), cancel: gettext('Cancel') };
  title = gettext('Unassign');
  status: StatusType = Status.WARNING;

  constructor(
    private translateService: TranslateService,
    private gainsightService: GainsightService
  ) {}

  ngOnInit() {
    this.message = this.translateService.instant(
      gettext('You are about to unassign "{{name}}". Do you want to proceed?'),
      { name: this.asset.name }
    );
  }

  async ngAfterViewInit() {
    try {
      await this.modalRef.result;
      this.onClose();
    } catch (error) {
      this.onDismiss();
    }
  }

  onClose() {
    this.gainsightService.triggerEvent(PRODUCT_EXPERIENCE.EVENT, {
      component: PRODUCT_EXPERIENCE.UNASSIGN_MODAL.COMPONENTS.UNASSIGN_MODAL,
      result: PRODUCT_EXPERIENCE.UNASSIGN_MODAL.RESULTS.ASSET_UNASSIGNED,
      url: this.CURRENT_LOCATION
    });
    this.closeSubject.next(true);
    this.closeSubject.complete();
  }

  onDismiss() {
    this.gainsightService.triggerEvent(PRODUCT_EXPERIENCE.EVENT, {
      component: PRODUCT_EXPERIENCE.UNASSIGN_MODAL.COMPONENTS.UNASSIGN_MODAL,
      action: PRODUCT_EXPERIENCE.UNASSIGN_MODAL.ACTIONS.CANCEL,
      url: this.CURRENT_LOCATION
    });
    this.closeSubject.complete();
  }
}
