import {
  Component,
  ElementRef,
  EventEmitter,
  HostListener,
  Input,
  Output,
  ViewChild
} from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { IManagedObject } from '@c8y/client';
import {
  AlertService,
  C8yStepper,
  GainsightService,
  Pagination,
  Permissions,
  gettext
} from '@c8y/ngx-components';
import { Subscription } from 'rxjs';
import { PRODUCT_EXPERIENCE } from '../shared/sub-assets.model';
import { SubAssetsService } from '../sub-assets.service';
import { AddGroupService } from './add-group.service';

@Component({
  selector: 'c8y-add-group',
  templateUrl: './add-group.component.html'
})
export class AddGroupComponent {
  @Input() currentGroupId: string;
  @Input() refresh = new EventEmitter<any>();
  @Output() onDeviceQueryStringChange: EventEmitter<string> = new EventEmitter<string>();
  @Output() onCancel = new EventEmitter<any>();
  @ViewChild(C8yStepper, { static: false })
  stepper: C8yStepper;
  @ViewChild('nameRef', { static: false })
  nameInputRef: ElementRef;
  deviceQueryStringOutput: string;
  formGroupStepOne: FormGroup;
  pendingStatus = false;
  pagination: Pagination = { pageSize: 20, currentPage: 1 };
  selected: string[] = [];
  subscription: Subscription;
  canCreateGroup = false;
  canAssignDevice = false;
  PRODUCT_EXPERIENCE = PRODUCT_EXPERIENCE;

  readonly ITEMS_SELECT_LIMIT: number = 15;
  readonly btnLabels = {
    next: gettext('Next'),
    cancel: gettext('Cancel'),
    create: gettext('Create')
  };

  private nameInput: HTMLInputElement;

  constructor(
    private fb: FormBuilder,
    private addGroupService: AddGroupService,
    private alert: AlertService,
    private subAssetsService: SubAssetsService,
    private gainsightService: GainsightService,
    private permissionsService: Permissions
  ) {}

  @HostListener('document:keydown.enter', ['$event']) onEnterKeyDown(_event: KeyboardEvent) {
    // Order matters! Needs to be placed before this.stepper.next
    if ((this.isGroupDetailsStep() && !this.canAssignDevice) || this.isAssignDeviceStep()) {
      this.createGroup();
      return;
    }
    this.stepper.next();
  }

  @HostListener('document:keydown.escape', ['$event']) onEscapeKeyDown(_event: KeyboardEvent) {
    this.onCancel.emit();
  }

  async ngOnInit() {
    this.formGroupStepOne = this.fb.group({
      name: ['', Validators.required],
      description: ['']
    });
    this.subscription = this.onCancel.subscribe(() => this.resetStepper());
    this.canCreateGroup =
      this.subAssetsService.canCreateGroup() ||
      (await this.permissionsService.canEdit(['ROLE_INVENTORY_ADMIN', 'ROLE_INVENTORY_CREATE'], {
        id: this.currentGroupId
      }));
    this.canAssignDevice = await this.subAssetsService.canAssignDevice({
      id: this.currentGroupId
    } as IManagedObject);
  }

  ngAfterViewInit() {
    this.nameInput = this.nameInputRef.nativeElement as HTMLInputElement;
    this.setFocusOnNameInput();
  }

  async createGroup() {
    if (this.canCreateGroup === false) {
      return;
    }

    this.pendingStatus = true;
    await this.addGroupService.createGroupAndAssignDevices(
      this.formGroupStepOne.value,
      this.currentGroupId,
      this.selected
    );

    this.pendingStatus = false;
    this.resetStepper();
    const alertMsg = gettext('Group created.');
    this.gainsightService.triggerEvent(PRODUCT_EXPERIENCE.EVENT, {
      component: PRODUCT_EXPERIENCE.ADD_GROUP.COMPONENTS.ADD_GROUP,
      url: window.location.href,
      result: PRODUCT_EXPERIENCE.ADD_GROUP.RESULTS.ADD_SUCCESS
    });
    this.alert.success(alertMsg);
    this.refresh.emit();
    this.onCancel.emit();
  }

  onSelected(selectedDevicesIDs: string[]) {
    this.selected = selectedDevicesIDs;
  }

  resetStepper() {
    this.stepper.reset();
    this.stepper.selectedIndex = 1;
    this.selected = [];
  }

  ngOnDestroy() {
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
  }

  private isGroupDetailsStep() {
    return this.stepper.selectedIndex === 0;
  }

  private isAssignDeviceStep() {
    return this.stepper.selectedIndex === 1;
  }

  private setFocusOnNameInput() {
    if (this.nameInput) {
      this.nameInput.focus();
      this.nameInput.select();
    }
  }
}
