import { Component, EventEmitter, Inject, OnInit } from '@angular/core';
import { Column, Permissions } from '@c8y/ngx-components';
import { SubAssetsConfig, SUB_ASSETS_CONFIG } from './sub-assets.model';
import { SubAssetsService } from './sub-assets.service';

@Component({
  selector: 'c8y-groups-name',
  templateUrl: './groups.component.html'
})
export class GroupsComponent implements OnInit {
  refresh: EventEmitter<any> = new EventEmitter();
  showAddGroup = false;
  filterable = true;
  sortable = true;
  shouldDisableAddGroup = false;
  columns: Column[] = [];

  constructor(
    private permissions: Permissions,
    private subAssetsService: SubAssetsService,
    @Inject(SUB_ASSETS_CONFIG) public moduleConfig: SubAssetsConfig
  ) {}

  ngOnInit() {
    if (!this.permissions.hasRole('ROLE_INVENTORY_READ')) {
      this.sortable = false;
    }
    this.shouldDisableAddGroup = !this.subAssetsService.canCreateGroup();

    this.columns = this.subAssetsService
      .getDefaultColumns()
      .filter(column => column.name !== 'alarms');
  }
}
