import { Component, Input, ViewChild } from '@angular/core';
import { IManagedObject } from '@c8y/client';
import {
  ConfirmModalComponent,
  GainsightService,
  gettext,
  ModalLabels,
  Status,
  StatusType
} from '@c8y/ngx-components';
import { TranslateService } from '@ngx-translate/core';
import { Subject } from 'rxjs';
import { PRODUCT_EXPERIENCE } from '../shared/sub-assets.model';
import { DeleteModalCheckboxes } from './delete-assets-modal.model';

@Component({
  selector: 'c8y-delete-assets-modal',
  templateUrl: './delete-assets-modal.component.html'
})
export class DeleteAssetsModalComponent {
  CURRENT_LOCATION = location.href;
  PRODUCT_EXPERIENCE = PRODUCT_EXPERIENCE;

  @Input() showWithCascadeCheckbox = true;
  @Input() showWithDeviceUserCheckbox = false;
  @Input() asset: IManagedObject;
  @ViewChild('modalRef', { static: false }) modalRef: ConfirmModalComponent;
  closeSubject: Subject<DeleteModalCheckboxes> = new Subject();
  labels: ModalLabels = { ok: gettext('Delete'), cancel: gettext('Cancel') };
  title: string = gettext('Delete');
  status: StatusType = Status.DANGER;
  config: DeleteModalCheckboxes = {
    cascade: false,
    withDeviceUser: false
  };
  message: string;
  deleteGroupSubAssetsMsg: string;

  constructor(
    private translateService: TranslateService,
    private gainsightService: GainsightService
  ) {}

  ngOnInit() {
    this.setModalTexts();
  }

  async ngAfterViewInit() {
    try {
      await this.modalRef.result;
      this.onClose();
    } catch (error) {
      this.onDismiss();
    }
  }

  onClose() {
    this.gainsightService.triggerEvent(PRODUCT_EXPERIENCE.EVENT, {
      component: PRODUCT_EXPERIENCE.DELETE_ASSET.COMPONENTS.DELETE_ASSETS_MODAL,
      result: PRODUCT_EXPERIENCE.DELETE_ASSET.RESULTS.DELETED,
      url: this.CURRENT_LOCATION
    });
    this.closeSubject.next(this.config);
    this.closeSubject.complete();
  }

  onDismiss() {
    this.gainsightService.triggerEvent(PRODUCT_EXPERIENCE.EVENT, {
      component: PRODUCT_EXPERIENCE.DELETE_ASSET.COMPONENTS.DELETE_ASSETS_MODAL,
      result: PRODUCT_EXPERIENCE.DELETE_ASSET.RESULTS.CANCELED,
      url: this.CURRENT_LOCATION
    });
    this.closeSubject.complete();
  }

  private setModalTexts() {
    this.message = this.translateService.instant(
      gettext(
        'You are about to delete: "{{name}}". This operation is irreversible. Do you want to proceed?'
      ),
      { name: this.asset.name }
    );
    this.deleteGroupSubAssetsMsg = this.translateService.instant(
      gettext('Also delete all devices inside "{{name}}" and its subassets.'),
      { name: this.asset.name }
    );
  }
}
