import { Inject, Injectable, Optional } from '@angular/core';
import {
  AbstractConfigurationStrategy,
  DATA_GRID_CONFIGURATION_CONTEXT,
  DATA_GRID_CONFIGURATION_CONTEXT_PROVIDER,
  GridConfig,
  GridConfigContext,
  GridConfigContextProvider,
  UserPreferencesConfigurationStrategy
} from '@c8y/ngx-components';
import { cloneDeep } from 'lodash-es';
import { Observable, of } from 'rxjs';
import { SubAssetsGridConfigContext } from './sub-assets-grid-configuration-strategy.service';

@Injectable({ providedIn: 'root' })
export class SmartGroupGridConfigurationStrategy extends AbstractConfigurationStrategy {
  constructor(
    protected userPreferencesConfigurationStrategy: UserPreferencesConfigurationStrategy,
    @Inject(DATA_GRID_CONFIGURATION_CONTEXT)
    @Optional()
    protected context: GridConfigContext,
    @Inject(DATA_GRID_CONFIGURATION_CONTEXT_PROVIDER)
    @Optional()
    protected contextProvider: GridConfigContextProvider
  ) {
    super(context, contextProvider);
  }

  getConfig$(context?: SubAssetsGridConfigContext): Observable<GridConfig> {
    const group = cloneDeep(this.retrieveContext(context)?.group);

    if (group?.c8y_DeviceColumnsConfig?.columns?.length) {
      group.c8y_DeviceColumnsConfig.columns = group.c8y_DeviceColumnsConfig.columns.map(column => {
        delete column.filter;
        return column;
      });
    }

    return of(group?.c8y_DeviceColumnsConfig);
  }

  saveConfig$(config: GridConfig, _context?: SubAssetsGridConfigContext): Observable<GridConfig> {
    return of(config);
  }
}
