import { Inject, Injectable, Optional } from '@angular/core';
import { IManagedObject } from '@c8y/client';
import {
  AbstractConfigurationStrategy,
  DataGridConfigurationStrategy,
  DATA_GRID_CONFIGURATION_CONTEXT,
  DATA_GRID_CONFIGURATION_CONTEXT_PROVIDER,
  GridConfig,
  GridConfigContext,
  GridConfigContextProvider,
  UserPreferencesConfigurationStrategy,
  UserPreferencesGridConfigContext
} from '@c8y/ngx-components';
import { AssetNodeService } from '@c8y/ngx-components/assets-navigator';
import { Observable } from 'rxjs';
import { SmartGroupGridConfigurationStrategy } from './smart-group-grid-configuration-strategy.service';

export interface SubAssetsGridConfigContext extends UserPreferencesGridConfigContext {
  group: IManagedObject;
}

@Injectable({ providedIn: 'root' })
export class SubAssetsGridConfigurationStrategy extends AbstractConfigurationStrategy {
  constructor(
    protected userPreferencesConfigurationStrategy: UserPreferencesConfigurationStrategy,
    protected smartGroupGridConfigurationStrategy: SmartGroupGridConfigurationStrategy,
    protected assetNodeService: AssetNodeService,
    @Inject(DATA_GRID_CONFIGURATION_CONTEXT)
    @Optional()
    protected context: GridConfigContext,
    @Inject(DATA_GRID_CONFIGURATION_CONTEXT_PROVIDER)
    @Optional()
    protected contextProvider: GridConfigContextProvider
  ) {
    super(context, contextProvider);
  }

  getConfig$(context?: SubAssetsGridConfigContext): Observable<GridConfig> {
    return this.getStrategy(context).getConfig$(context);
  }

  saveConfig$(config: GridConfig, context?: SubAssetsGridConfigContext): Observable<GridConfig> {
    return this.getStrategy(context).saveConfig$(config, context);
  }

  private getStrategy(ctx?: SubAssetsGridConfigContext): DataGridConfigurationStrategy {
    const context = this.retrieveContext(ctx) as SubAssetsGridConfigContext;
    return !!context?.group &&
      this.assetNodeService.isDynamicGroup(context?.group) &&
      context?.group?.c8y_DeviceColumnsConfig
      ? this.smartGroupGridConfigurationStrategy
      : this.userPreferencesConfigurationStrategy;
  }
}
